code[class*="language-"],
pre[class*="language-"] {
	font-family: Consolas, Monaco, "Andale Mono", monospace;
	line-height: 1.5;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	-moz-tab-size: 4;
	tab-size: 4;
	-webkit-hyphens: none;
	-moz-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
	color: #cdd3de;
	direction: ltr;
}

/* code blocks */
pre[class*="language-"] {
	overflow: auto;
	margin: 0.5rem 0;
	padding: 1rem;
}

#namespace {
	opacity: 0.7;
}

.token,
.token.operator,
.token.property,
.language-scss .token.boolean,
.token.doctype,
.token.cdata,
.token.punctuation {
	color: #cdd3de;
}

.token.class-name {
	color: #871045;
}

.token.constant {
	color: #87676a;
}

.token.symbol {
	color: #a210de;
}

.token.char {
	color: #87ffde;
}

.token.regex {
	color: #8cc0de;
}

.token.builtin {
	color: #ebb0de;
}

.token.prolog {
	color: #87100e;
}

.token.entity {
	color: #87dd00;
	cursor: help;
}

.token.placeholder {
	color: #0d1ede;
}

.token.atrule,
.token.selector,
.token.keyword,
.token.important {
	color: #c792ea;
}

.token.comment {
	color: #65737e;
}

.language-scss .token.selector,
.language-css .token.selector .class,
.language-css .token.selector .pseudo-class,
.language-css .token.selector .pseudo-element,
.token.number,
.token.tag .attr-name {
	color: #ffcb6b;
}

.language-css .token.selector .id {
	color: #89ddff;
}

.token.url,
.token.hexcode,
.token.function {
	color: #80cbc4;
}

.token.variable,
.language-css .token.selector,
.token.tag,
.token.deleted,
.token.namespace {
	color: #f77669;
}

.token.bold {
	font-weight: 700;
}

.token.italic {
	font-style: italic;
}

.token.label {
	color: null;
}

.token.string,
.token.tag .attr-value,
.token.tag .attr-value .punctuation {
	color: #c3e88d;
}

/*
Atom One Dark by Daniel Gamage
Original One Dark Syntax theme from https://github.com/atom/one-dark-syntax
base:    #282c34
mono-1:  #abb2bf
mono-2:  #818896
mono-3:  #5c6370
hue-1:   #56b6c2
hue-2:   #61aeee
hue-3:   #c678dd
hue-4:   #98c379
hue-5:   #e06c75
hue-5-2: #be5046
hue-6:   #d19a66
hue-6-2: #e6c07b
*/
.hljs {
	background: #282c34;
	color: #abb2bf;
	display: block;
	overflow-x: auto;
	padding: 0.5em;
}

.hljs-comment,
.hljs-quote {
	font-style: italic;
	color: #5c6370;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
	color: #c678dd;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
	color: #e06c75;
}

.hljs-literal {
	color: #56b6c2;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
	color: #98c379;
}

.hljs-built_in,
.hljs-class .hljs-title {
	color: #e6c07b;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
	color: #d19a66;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
	color: #61aeee;
}

.hljs-emphasis {
	font-style: italic;
}

.hljs-strong {
	font-weight: bold;
}

.hljs-link {
	text-decoration: underline;
}
